#header{
    background-color: red;
    height:100px;
    width:100%
}

.logoListing{
    height:50px;
    width:50px;
}

a{
    text-decoration: none;
}

#headingListing{
    color:red;
    margin: 26%;
}

#social{
    display: inline-block;
    margin-left:75%;
    margin-top:-4%
}

#logo_main{
    height:70px;
    width:70px;
    background-color: white;
    border-radius:50%;
    margin-left:80px;
    display: inline-block; 
    margin-top: 1%;
}

#mainListing{
    margin-left: 10%;
    margin-top: 5%;
}

#filter{
    height:600px;
    width:19%;
    float: left;
    border: 5px solid gray;
}
#content{
    width:65%;
    float: left;
    margin-left: 5%;
    margin-top: -1%;

}

.list_content{
    width:100%;
    height: 200px;
    border: 5px solid gray;
    margin-top: 2%;
}

.item {
    width: 685px;
    height: 340px;
    box-shadow: 0 5px 10px 0 rgb(124, 211, 240);
    background-color: lightblue;
    margin-bottom: 20px;
}


.Image {
    width: 250px;
    height: 270px;
    border-radius: 23px;
    background-color: #ea6060;
    margin-left:5%;
    margin-top: 30px ;
}


.hotel_name {
    width: 100%;
    margin-left:5%;
    height: 43px;
    font-family: Poppins;
    font-size: 25px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}

.city_name {
    width: 100%;
    height: 23px;
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}
.rating {
    line-height: 11;
}
.cost {
    line-height: 5;
}

